* {
  font-family: 'Open Sans', sans-serif; }

@keyframes donut-spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

.donut {
  display: inline-block;
  border: 4px solid rgba(0, 0, 0, 0.15);
  border-left-color: rgba(0, 0, 0, 0.75);
  border-radius: 50%;
  width: 15px;
  height: 15px;
  animation: donut-spin 1.2s linear infinite; }

.cs-hide {
  display: none !important; }

.setup {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .setup .mdc-card {
    width: 65%;
    max-width: 900px;
    height: auto;
    min-height: 500px;
    position: relative; }
    @media only screen and (max-width: 992px) {
      .setup .mdc-card {
        width: 90%; } }
  .setup__footer {
    display: flex;
    align-items: center;
    margin-top: auto; }
    .setup__footer .mdc-button {
      margin-left: auto; }

.setup-card {
  display: flex; }
  .setup-card__form {
    width: 60%;
    height: auto;
    min-height: 500px;
    padding: 32px; }
    @media only screen and (max-width: 768px) {
      .setup-card__form {
        width: 100%; } }
    .setup-card__form form {
      margin-top: 16px;
      display: flex;
      flex-direction: column; }
    .setup-card__form .text-field-container {
      width: 100%;
      margin-top: 32px; }
    .setup-card__form .mdc-text-field-helper-line {
      padding: 0px; }
    .setup-card__form .mdc-text-field {
      width: 100%;
      height: 40px; }
      .setup-card__form .mdc-text-field .mdc-text-field__input {
        font-size: 14px;
        padding: 7px 11px;
        padding-right: 40px; }
      .setup-card__form .mdc-text-field .mdc-floating-label {
        font-size: 14px;
        top: 11px; }
      .setup-card__form .mdc-text-field .mdc-floating-label--float-above {
        transform: translateY(-150%) scale(0.75) !important; }
      .setup-card__form .mdc-text-field .id-status {
        position: absolute;
        right: 0;
        margin: 11px 12px;
        font-size: 12px;
        color: #333648;
        font-weight: 600; }
      .setup-card__form .mdc-text-field #cruxpayId {
        padding-right: 100px; }
    .setup-card__form .validation-rules {
      margin-top: 12px;
      opacity: 0; }
      .setup-card__form .validation-rules .rule {
        display: flex;
        align-items: center;
        margin-top: 8px; }
        .setup-card__form .validation-rules .rule .material-icons {
          font-size: 16px;
          margin-right: 12px; }
        .setup-card__form .validation-rules .rule span {
          font-weight: 400;
          font-size: 12px; }
      .setup-card__form .validation-rules .rule-isValid .icon-invalid {
        display: none; }
      .setup-card__form .validation-rules .rule-isValid .icon-valid {
        display: block; }
      .setup-card__form .validation-rules .rule-isValid .material-icons {
        color: #2ed573; }
      .setup-card__form .validation-rules .rule-isValid span {
        color: #2ed573; }
      .setup-card__form .validation-rules .rule-isInvalid .icon-invalid {
        display: block; }
      .setup-card__form .validation-rules .rule-isInvalid .icon-valid {
        display: none; }
      .setup-card__form .validation-rules .rule-isInvalid .material-icons {
        color: #eb4d4b; }
      .setup-card__form .validation-rules .rule-isInvalid span {
        color: #eb4d4b; }
    .setup-card__form .helper-text--success {
      color: #736df8 !important; }
  .setup-card__description {
    width: 40%;
    height: auto;
    min-height: 500px;
    padding: 32px;
    display: flex;
    flex-direction: column; }
    .setup-card__description img {
      display: block;
      margin: auto; }
    @media only screen and (max-width: 768px) {
      .setup-card__description {
        display: none; } }
  .setup-card__brand {
    color: #333648;
    font-weight: 700;
    font-size: 24px; }
  .setup-card__headline {
    color: #333648;
    font-weight: 400;
    font-size: 24px;
    padding-top: 16px;
    text-align: left; }
  .setup-card__body {
    color: #333648;
    font-weight: 400;
    font-size: 16px;
    margin-top: 8px; }

.setup-close_button {
  --mdc-ripple-fg-size: 28px;
  --mdc-ripple-fg-scale: 1.71429;
  --mdc-ripple-left: 10px;
  --mdc-ripple-top: 10px;
  position: absolute;
  right: 0;
  font-size: 20px;
  color: #666666; }

.customisation {
  display: flex; }
  .customisation__selection {
    width: 50%;
    height: auto;
    min-height: 500px;
    padding: 32px 32px 8px 32px;
    border-right: 1px solid #e3e8ef;
    display: flex;
    flex-direction: column; }
  .customisation__description {
    width: 50%;
    height: auto;
    min-height: 500px;
    padding: 32px;
    display: flex;
    flex-direction: column; }
  .customisation__brand {
    color: #333648;
    font-weight: 700;
    font-size: 24px; }
  .customisation__headline {
    color: #333648;
    font-weight: 400;
    font-size: 18px;
    margin-top: 8px; }
  .customisation__search {
    margin: 24px 0px; }
    .customisation__search .mdc-text-field {
      width: 100%;
      height: 40px; }
      .customisation__search .mdc-text-field .mdc-text-field__input {
        font-size: 14px;
        padding: 7px 11px;
        padding-right: 40px; }
      .customisation__search .mdc-text-field .mdc-floating-label {
        font-size: 14px;
        top: 18px; }
      .customisation__search .mdc-text-field .mdc-floating-label--float-above {
        transform: translateY(-150%) scale(0.75) !important; }
      .customisation__search .mdc-text-field .mdc-text-field__icon {
        top: 20px;
        right: 16px;
        font-size: 18px;
        color: #8a8a8a; }
  .customisation__currency {
    width: 99%;
    min-height: 45px;
    display: flex;
    align-items: center;
    border: 1px solid #dadada;
    border-radius: 4px;
    margin: 16px 0px; }
  .customisation__currency-list {
    height: 325px;
    overflow-x: hidden;
    overflow-y: scroll; }
  .customisation__currency-logo {
    width: 20px;
    height: 20px;
    margin-left: 16px; }
  .customisation__currency-name {
    color: #333648;
    font-size: 16px;
    margin-left: 16px;
    margin-right: 16px;
    font-family: 'Open Sans', sans-serif; }
  .customisation__currency-symbol {
    color: #333648;
    font-size: 16px;
    font-weight: 700;
    margin-left: auto;
    margin-right: 16px; }
  .customisation__description-id {
    margin-bottom: 16px; }
  .customisation .description-heading {
    color: #8a8a8a;
    font-weight: 700;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 0.0625em; }
  .customisation .description-id {
    color: #333648;
    font-size: 20px;
    margin-top: -3px; }
  .customisation__selected-currencies {
    margin-top: 32px; }
  .customisation__selected-currencies-list {
    display: flex;
    flex-flow: row wrap; }
  .customisation .selected-currency__pill {
    min-width: 100px;
    height: 35px;
    border-radius: 1000px;
    background-color: #f3f5f8;
    color: #333648;
    display: flex;
    align-items: center;
    padding: 0px 8px;
    margin: 12px 12px 12px 0px; }
    .customisation .selected-currency__pill strong {
      margin-left: 16px;
      margin-right: auto;
      font-size: 14px; }
    .customisation .selected-currency__pill .material-icons {
      font-size: 18px;
      cursor: pointer;
      margin-right: 3px; }

.mdc-crux-action .donut {
  width: 12px;
  height: 12px;
  margin-left: 8px;
  display: none; }

.mdc-crux-action:disabled .donut {
  display: block; }

.brand-logo {
  width: 150px; }

/*****************
*** Overrides ***
*****************/
.customisation__currency .mdc-form-field {
  width: 100%;
  padding: 8px; }

.customisation__currency-logo {
  margin-left: 0px; }

.customisation__checkbox-label {
  padding: 0px;
  margin: 0px;
  flex-grow: 1;
  cursor: pointer; }
  .customisation__checkbox-label--inner {
    display: flex;
    align-items: center;
    margin-left: 12px; }
  .customisation__checkbox-label--address {
    word-break: break-all;
    margin-top: 8px; }
